import test from "ava"
import request from "supertest"
import app from "../app"

test ('Get list of applicants', async t => {
    const applicantToCreate = { name: 'Elena', surname: 'Volovicheva', location: 'Berlin', interests: [], applications: [] }

    const creation = await request(app)
    .post('/applicant')
    .send(applicantToCreate)

    const res = await request(app).get('./applicant/all')

    t.is(res.status, 200)
    t.true(Array.isArray(res.body), "Body should be an array")
    t.true(res.body.length > 0)
})

test('Create a new applicant', async t => {
    t.plan(6)

    const applicantToCreate = { name: 'Elena', surname: 'Volovicheva', location: 'Berlin', interests: [], applications: [] }

    const res = await request(app)
    .post('/applicant')
    .send(applicantToCreate)

    t.is(res.status, 200)
    t.is(res.body.name, applicantToCreate.name)
    t.is(res.body.surname, applicantToCreate.age)
    t.is(res.body.location, applicantToCreate.location)
    t.is(res.body.interests, applicantToCreate.interests)
    t.is(res.body.applications, applicantToCreate.applications)
})